<template>
    <div class="Payment">
        <main>
            <div class="weui-cells weui-cells_form">
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        15816388600
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        2016 - 07 - 01
                        <span class="time">15:24:28</span>
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        2016 - 07 - 01
                        <span class="time">15:24:28</span>
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        808
                    </div>
                </div>
                <div class="weui-cell weui-cell_select weui-cell_select-after mode" @click="abcd">
                    <div class="weui-cell__hd" style="color:#b2b2b2">
                        
                    </div>
                    <div class="weui-cell__bd">
                        <p v-if="!PaymentMethod"></p>
                        {{PaymentMethod}}
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd">
                        <label class="weui-label"></label>
                    </div>
                    <div class="weui-cell__bd">
                        
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        
                    </div>
                </div>
                <div class="weui-flex">
                    <div>
                        <div class="foodname"></div>
                    </div>
                    <div class="weui-flex__item">
                        <div class="placeholder">
                            <span>X1</span>
                        </div>
                    </div>
                    <div>
                        <div class="money">10.00</div>
                    </div>
                </div>
            </div>
        </main>
        <div class="totalSum">
            <p>
                <span class="Price">1,636.00</span>
            </p>
            <div class="btn">
                <a href="javascript:;" class="weui-btn weui-btn_primary"></a>
                <a href="javascript:;" class="weui-btn weui-btn_primary"></a>
            </div>
        </div>
    </div>
</template>

<script type='text/ecmascript-6'>
import weui from "@/assets/js/weui.min";
export default {
    data() {
        return {
            value: '',
            PaymentMethod: ''
        }
    },
    methods: {
        abcd() {
            var _this = this;
            weui.picker([{
                label: '',
                value: 0
            }, {
                label: '',
                value: 1
            }, {
                label: '',
                value: 2
            }], {
                    onChange: function(result) {
                        // console.log(result, '1111');
                    },
                    onConfirm: function(result) {
                        _this.PaymentMethod = result[0].label
                    }
                });
        },

    },
}
</script>

<style scoped lang='less' rel='stylesheet/less'>
.Payment {
    display: flex;
    flex-flow: column;
    min-height: 100vh;
}

main {
    flex: 1;
    background: #f4f4f4;
}
</style>



